export default{
	data(){
		return{
			id:'',
			qrimg:'',
			windowHeight: '',
		}
	},
	onLoad() {
		var that=this;
		that.windowHeight = this.statusBarHeight + 44;
		uni.getStorage({
			key:'userinfo',
			success:function(res) {
				that.id=res.data.id;
				that.getinfo();
			}
		})
	},
	methods:{
		getinfo:function(){
			var that=this;
			uni.request({
				url: that.serveipd+'/api/merchant/auth/info/'+that.id,
				method: 'GET',
				data: {
					id:that.id
				},
				success: res => {
					that.qrimg=res.data.data.qrcodeUrl;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		back: function() {
			uni.navigateBack({
				delta: 1
			});
		},
	}
	
}
